<template>
    <div class="line">
        <div class="grayLine valueLine"></div>
        <div class="blueLine valueLine" :style="'width: '+percen+'%'"></div>
    </div>
</template>
<style lang="css" scope>
    .line{
        min-width: 170px;
        display: flex;
        position: relative;
        height: 15px;
        width: 100%;
        border-radius: 5px;
    }
    .line .valueLine{
        height: auto;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 5px;
    }
    .line .grayLine{
        background: gray;
        width: 100%;
        
    }
    .line .blueLine{
        background: blue;
        
    }
</style>
<script>
export default {
  props: {
    percen: {
      type: Number,
      required: true
    }
  }
}
</script>


